                                <b-card-text>
                                    <h3>Описание</h3>
                                    <p>Целями проведения ежегодного конкурса "Проекты технологической инициативы" и
                                        предоставления гранта являются комплексное развитие региональной инновационной
                                        системы и создание благоприятных условий для активизации инновационной
                                        деятельности на основе использования научно-технического потенциала.</p>
                                    <p>Грант предоставляется по каждой из следующих номинаций:</p>
                                    <ul>
                                        <li>"Фуднет". Принимаются заявки, в которых содержится описание инновационных
                                            проектов, направленных на развитие рынка производства и реализации
                                            питательных веществ и конечных видов пищевых продуктов (персонализированных
                                            и общих, на основе традиционного сырья и его заменителей), а также
                                            сопутствующих IT-решений. Ключевые сегменты данного рынка - индивидуальное
                                            персонализированное питание, геномика, альтернативные источники сырья,
                                            точное земледелие и органическое сельское хозяйство;
                                        </li>
                                        <li>"Хелснет". Принимаются заявки, в которых содержится описание инновационных
                                            проектов, направленных на развитие рынка персонализированных медицинских
                                            услуг и лекарственных средств, обеспечивающих рост продолжительности жизни,
                                            а также получение новых эффективных средств профилактики и лечения различных
                                            заболеваний. Ключевые сегменты данного рынка - превентивная медицина, спорт
                                            и здоровье, медицинская генетика, информационные технологии в медицине,
                                            здоровое долголетие, биотехнологии в медицине;
                                        </li>
                                        <li>"Энерджинет". Принимаются заявки, в которых содержится описание
                                            инновационных проектов, направленных на развитие рынка оборудования,
                                            программного обеспечения, инжиниринговых и сервисных услуг для
                                            разномасштабных комплексных систем и сервисов интеллектуальной энергетики.
                                            Ключевые сегменты данного рынка - надежные и гибкие распределительные сети,
                                            интеллектуальная распределенная энергетика, персональная энергетика и
                                            потребительские сервисы;
                                        </li>
                                        <li>"Технет". Принимаются заявки, в которых содержится описание инновационных
                                            проектов, обеспечивающих технологическую поддержку развития рынков
                                            Национальной технологической инициативы и высокотехнологичных отраслей
                                            промышленности. Ключевые направления - цифровое проектирование и
                                            моделирование, новые материалы (передовые сплавы, полимеры, композиционные и
                                            керамические материалы, металлопорошковые композиции и другие), аддитивные,
                                            гибридные технологии, числовое программное управление, компоненты
                                            робототехники и сенсорика, информационные системы управления предприятием,
                                            большие данные ("Big Data"), индустриальный интернет.
                                        </li>
                                    </ul>
                                    <p>Получатель гранта определяется по итогам конкурса, который проводится исходя из
                                        наилучших условий достижения целей (результатов) предоставления гранта.</p>
                                </b-card-text>
                            </b-tab>
                            <b-tab title="Требования к заявителю">
                                <b-card-text>
                                    <h3>Требования к заявителю</h3>
                                    <p>Категория получателей гранта – юридические лица и индивидуальные предприниматели,
                                        осуществляющие инновационную и предпринимательскую деятельность (далее –
                                        Участники Конкурса).</p>
                                    <p>Участники Конкурса на дату подачи заявки должны соответствовать следующим
                                        требованиям:</p>
                                    <ul>
                                        <li>не должны находиться в процессе реорганизации (за исключением
                                            реорганизации в форме присоединения к юридическому лицу, являющемуся
                                            участником Конкурса, другого юридического лица), ликвидации, в отношении них
                                            не введена процедура банкротства, деятельность участника Конкурса не
                                            приостановлена в порядке, предусмотренном законодательством Российской
                                            Федерации (для юридических лиц);
                                        </li>
                                        <li>не прекратили деятельность в качестве индивидуального предпринимателя (для
                                            индивидуальных предпринимателей);
                                        </li>
                                        <li>не должны являться иностранными юридическими лицами, а также российскими
                                            юридическими лицами, в уставном (складочном) капитале которых доля участия
                                            иностранных юридических лиц, местом регистрации которых является государство
                                            или территория, включенные в утвержденный Министерством финансов Российской
                                            Федерации перечень государств и территорий, предоставляющих льготный
                                            налоговый режим налогообложения и (или) не предусматривающих раскрытия и
                                            предоставления информации при проведении финансовых операций (офшорные
                                            зоны), в совокупности превышает 50 процентов.
                                        </li>
                                    </ul>
